@mixin liststyl {
    color: #e5e7eb;
    border: 2px solid transparent;
    border-radius: 6px;
    padding: 0.6em;
}

@mixin hover {
    border: 2px solid white;
    background-color: rgba(74, 70, 70, 0.756);
    animation-duration: 500ms;
}

html {
    padding: 0;
    margin: 0;
    min-height: 100%;
    box-sizing: border-box;
    border-width: 0;
    border-style: solid;
    border-color: #e5e7eb;

    body {
        font-family: 'Courier New', Courier, monospace;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-image: url(../assets/img/wp4094503.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        margin: 0;
    }
}
